html {
  scroll-behavior: smooth;
}

body {
  color: #2c2c2c;
  min-width: 320px;
  background-color: #fefefe;
  font-family: Roboto, sans-serif;
}

body, h1, h2, h3, h4, p, ul, li {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (max-width: 480px) {
  .container {
    width: 100%;
  }
}

.header {
  background-color: #fefefe;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

.header__container {
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 767px) {
  .header__container {
    min-height: 72px;
    justify-content: space-between;
  }
}

@media screen and (min-width: 768px) {
  .navigation {
    margin-left: auto;
    margin-right: 30px;
  }
}

.navigation__list {
  justify-content: space-between;
  gap: 28px;
  margin-left: auto;
  padding: 0;
  list-style: none;
  display: flex;
}

@media (max-width: 767px) {
  .navigation__list {
    display: none;
  }
}

.navigation__link {
  letter-spacing: .02em;
  color: #2c2c2c;
  padding-top: 28px;
  padding-bottom: 28px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.15;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.navigation__link:hover, .navigation__link:focus {
  color: #bbf330;
  text-decoration: underline;
}

.header__button {
  width: 92px;
  height: 32px;
  color: #2c2c2c;
  cursor: pointer;
  background-color: #bbf330;
  border: none;
  border-radius: 30px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
}

@media (max-width: 767px) {
  .header__button {
    display: none;
  }
}

.hero-title {
